import Vuex from 'vuex'
import Vue from 'vue'
Vue.use(Vuex)
const actions = {
    addsum(context, value) {
        context.commit('ADDSUM', value)
    },
    desum(context, value) {
        context.commit('DESUM', value)
    },
    add_ji_sum(context, value) {
        if (context.state.sum % 2 != 0) {
            context.commit('ADD_JI_SUM', value)
        }
    },
    add_sum_late(context, value) {
        setTimeout(() => {
            context.commit('ADD_SUM_LATE', value)
        }, 500);
    }
}

const mutations = {
    ADDSUM(state, value) {
        state.sum += value;
    },
    DESUM(state, value) {
        state.sum -= value
    },
    ADD_JI_SUM(state, value) {
        state.sum += value;
    },
    ADD_SUM_LATE(state, value) {
        state.sum += value;
    }
}

const state = {
    sum: 0
}

export default new Vuex.Store({
    actions: actions,
    mutations: mutations,
    state: state
})