<template>
  <div>
    <h1>当前数值为：{{ $store.state.sum }}</h1>
    <!-- 动态绑定select框的值 -->
    <select v-model.number="num">
        <option value="1">1</option>
        <option value="2">2</option>
        <option value="3">3</option>
    </select>
    <button @click="addsum">+</button>
    <button @click="desum">-</button>
    <button @click="add_ji_sum">奇数才加</button>
    <button @click="add_sum_late">等一下再加</button>
  </div>
</template>

<script>
export default {
    name:'count',
    data(){
        return {
            num:1,
        }
    },
    methods:{
        addsum(){
            // this.sum+=this.num;
            this.$store.dispatch('addsum',this.num)
        },
        desum(){
            // this.sum-=this.num;
            this.$store.dispatch('desum',this.num)
        },
        add_ji_sum(){
            // if(this.sum%2!=0){
            //     this.sum+=this.num;
            // }
            this.$store.dispatch('add_ji_sum',this.num)

        },
        add_sum_late(){
            // setTimeout(()=>{
            //     this.sum+=this.num;
            // },500)
            this.$store.dispatch('add_sum_late',this.num)

        }
    },
    mounted(){
        console.log('count',this.$store.state);
    }
}
</script>

